"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";
import { motion } from "framer-motion";
import Image from "next/image";
import { Eye, EyeOff } from "lucide-react";
import signupImage from "../../../public/signup.jpg";

export default function Signup() {
  const router = useRouter();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [errors, setErrors] = useState({ username: "", password: "", confirmPassword: "" });

  const validateUsername = (value: string): string => {
    return value.length < 8 ? "Username must be at least 8 characters long" : "";
  };

  const validatePassword = (value: string): string => {
    const regex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9]).{8,}$/;
    return !regex.test(value) ? "Password must contain one uppercase, one lowercase, and one special character" : "";
  };

  const validateConfirmPassword = (value: string): string => {
    return value !== password ? "Passwords do not match" : "";
  };

  const handleSignup = () => {
    const usernameError = validateUsername(username);
    const passwordError = validatePassword(password);
    const confirmPasswordError = validateConfirmPassword(confirmPassword);

    setErrors({ username: usernameError, password: passwordError, confirmPassword: confirmPasswordError });

    if (!usernameError && !passwordError && !confirmPasswordError) {
      router.push("/home");
    }
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-gray-900">
      <div className="flex w-[700px] bg-white shadow-lg rounded-2xl overflow-hidden p-6 flex-col items-center">
        <motion.div 
          className="bg-green-500 text-white text-xl font-bold px-6 py-2 rounded-lg mb-6"
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          Sign Up
        </motion.div>
        <Image src={signupImage} alt="Signup" className="w-40 h-40 object-cover mb-4" />
        <motion.div 
          className="w-full flex flex-col justify-center"
          initial={{ opacity: 0, x: 50 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.5 }}
        >
          <input 
            type="text" 
            placeholder="Username" 
            className="w-full p-3 border rounded-lg mb-2 text-black placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-green-500 transition-all duration-300" 
            value={username} 
            onChange={(e) => {
              setUsername(e.target.value);
              setErrors((prev) => ({ ...prev, username: validateUsername(e.target.value) }));
            }}
          />
          {errors.username && <p className="text-red-500 text-sm mb-2">{errors.username}</p>}
          <div className="relative w-full">
            <input 
              type={showPassword ? "text" : "password"} 
              placeholder="Password" 
              className="w-full p-3 border rounded-lg text-black placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-green-500 transition-all duration-300 pr-10" 
              value={password} 
              onChange={(e) => {
                setPassword(e.target.value);
                setErrors((prev) => ({ ...prev, password: validatePassword(e.target.value) }));
              }}
            />
            <button 
              className="absolute right-3 top-3 text-gray-500" 
              onClick={() => setShowPassword(!showPassword)}
            >
              {showPassword ? <EyeOff size={20} /> : <Eye size={20} />}
            </button>
          </div>
          {errors.password && <p className="text-red-500 text-sm mb-2">{errors.password}</p>}
          <input 
            type="password" 
            placeholder="Confirm Password" 
            className="w-full p-3 border rounded-lg mb-2 text-black placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-green-500 transition-all duration-300" 
            value={confirmPassword} 
            onChange={(e) => {
              setConfirmPassword(e.target.value);
              setErrors((prev) => ({ ...prev, confirmPassword: validateConfirmPassword(e.target.value) }));
            }}
          />
          {errors.confirmPassword && <p className="text-red-500 text-sm mb-2">{errors.confirmPassword}</p>}
          <button 
            className="w-full bg-green-500 text-white py-3 rounded-lg hover:bg-green-600 transition-transform transform hover:scale-105 mt-4" 
            onClick={handleSignup}
          >
            Sign Up
          </button>
          <p className="text-center mt-4 text-gray-500">
            Already have an account? <span className="text-blue-500 cursor-pointer hover:underline" onClick={() => router.push("/auth/login")}>
              Login
            </span>
          </p>
        </motion.div>
      </div>
    </div>
  );
}
